import React from "react";
import { Fragment } from 'react';
import './View.css'
import Photo from './Photo'
import { useReactToPrint } from 'react-to-print';
import  { useRef } from 'react';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';

function Input(props)
{
    console.log(props.name);
    console.log(props.ema);
    console.log(props.phon);
    console.log(props.pers);
    console.log(props.exp);
    console.log(props.educ);
    console.log(props.prof);
   function handlePrint()  {
    html2canvas(document.querySelector("#content")).then(canvas => {
      // if you want see your screenshot in body.
      const imgData = canvas.toDataURL('image/png');
      const pdf = new jsPDF("portrait", "mm", "a4");
      pdf.addImage(imgData, 'PNG', 0, 0);
      pdf.setFont("cursive");
      pdf.save("download.pdf"); 
  });  
  }
    return (
        <Fragment>
 <div id="content"></div>         
<div className="container" >
  <div className="header">
    <div className="full-name">
      <div className="photo">
         <Photo/>
      </div>  
    </div>
    <div className="contact-info">
    <span className="email">Name: </span>
    <span className="first-name">{props.name}</span> 
    <span className="separator"></span>
      <span className="email">Email: </span>
      <span className="email-val">{props.ema}</span>
      <span className="separator"></span>
      <span className="phone">Phone: </span>
      <span className="phone-val">{props.phon}</span>
    </div>
    
    <div className="about">
      <span className="position">Personal Details</span>
      <span className="desc">
         {props.pers}
      </span>
    </div>
  </div>
   <div className="details">
    <div className="section">
      <div className="section__title">Experience</div>
      <div className="section__list">
        <div className="section__list-item">
          <div className="left">
            <div className="addr">{props.exp}</div>
          </div>
          <div className="right">
            <div className="name"></div>
            <div className="desc"></div>
          </div>
        </div>
          <div className="right">
            <div className="name"></div>
            <div className="desc"></div>
          </div>
        </div>

      </div>
    </div>
    <div className="section">
      <div className="section__title">Education</div>
      <div className="section__list">
        <div className="section__list-item">
          <div className="left">
            <div className="addr">{props.educ}</div>
          </div>
          <div className="right">
            <div className="name"></div>
            <div className="desc"></div>
          </div>
        </div>
        <div className="section__list-item">
          <div className="right">
            <div className="name"></div>
            <div className="desc"></div>
          </div>
        </div>

      </div>
      
  </div>
     <div className="section">
      <div className="section__title">Projects</div> 
       <div className="section__list">
         <div className="section__list-item">
           <div className="text">{props.prof}</div>
         </div>
         
        
       </div>
    </div>
     <div className="section">
      
       
       </div>
       <div className="section__title">Skills</div>
       <div className="skills__item">
           <div className="left"><div className="text">
            {props.sk}</div>
         </div>
       </div>
     <div className="section">
     <div className="section__title">
       Interests
       </div>
       <div className="section__list">
         <div className="section__list-item">
                 {props.int}
          </div>
       </div>
     </div>
     
     </div>
        </Fragment>
       
    )
}
export default Input;

 