import './Pro.css';
import { Fragment } from 'react';
import React, {useRef,useState} from 'react';
import Input from './Input'
import {Link} from 'react-router-dom'
import {useHistory} from 'react-router-dom'
function ViewProfile()
{
    const history=useHistory();
    const name=useRef();
    const email=useRef();
    const phone=useRef();
    const personal=useRef();
    const experience=useRef();
    const edu=useRef();
    const pro=useRef();
    const skill=useRef();
    const inte=useRef();
    const[nam,enteredname]=useState('');
    const[ema,enteredemail]=useState('');
    const[phon,enteredphone]=useState('');
    const[pers,enteredpers]=useState('');
    const[exp,enteredexp]=useState('');
    const[educ,eneterededuc]=useState('');
    const[prof,enteredprof]=useState('');
    const[sk,enteredsk]=useState('');
    const[int,enteredint]=useState('');
    const[isLoggedIn,login]=useState(false);
    function f1(event)
    {
        event.preventDefault();
        enteredname(name.current.value);
        enteredemail(email.current.value);
        enteredphone(phone.current.value);
        enteredpers(personal.current.value);
        enteredexp(experience.current.value);
        eneterededuc(edu.current.value);
        enteredprof(pro.current.value);
        enteredsk(skill.current.value);
        enteredint(inte.current.value);   
        login(true);
        
        localStorage.setItem(1,nam+'');
        //history.replace('/login');
    }
    console.log(nam);
    return (
        <Fragment>
        {!isLoggedIn&&<div className="wrapper">
    <form className="form" onSubmit={f1}>
      <div className="pageTitle title">CV BUILDER</div>
      <div className="secondaryTitle title">Start your Career With Great Height..</div>
      <input type="text" className="name formEntry" placeholder="Name" ref={name}/>
      <input type="text" className="email formEntry" placeholder="Email" ref={email}/>
      <input type="text" className="email formEntry" placeholder="Phone Number" ref={phone}/>
      <textarea className="message formEntry" placeholder="Personal Details" ref={personal}></textarea>
      <textarea className="message formEntry" placeholder="Experience" ref={experience}></textarea>
      <textarea className="message formEntry" placeholder="Education" ref={edu}></textarea>
      <textarea className="message formEntry" placeholder="Projects" ref={pro}></textarea>
      <input type="text" className="email formEntry" placeholder="Skills" ref={skill}/>
      <input type="text" className="email formEntry" placeholder="Interest" ref={inte}/>
      <input type="checkbox" className="termsConditions" />
      <label style={{color:"gray"}} htmlFor="terms"> I Accept the <span style={{color: "#0e3721"}}>Terms of Use</span>  <span style={{color: "#0e3721"}}>Privacy Policy</span>.</label><br/>
      <button className="submit formEntry">Submit</button>
    </form>
    </div>}
    {isLoggedIn && <Input name={nam} ema={ema} phon={phon} pers={pers} exp={exp} educ={educ} prof={prof} sk={sk} int={int} ></Input>}
    </Fragment>
    )
}
export default ViewProfile;